

import React, {Component} from 'react';

import {Line} from 'react-chartjs';

class TestView extends Component {



    render() {
        let data = {
            data: {
                labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
                datasets: [{
                    label: '# of Votes',
                    data: [12, 19, 3, 5, 2, 3],
                }]
            },
            // options: {
            //     scales: {
            //         yAxes: [{
            //             ticks: {
            //                 beginAtZero:true
            //             }
            //         }]
            //     }
            // }
        };



        return (
            <div>
                <Line data={data.data} options={data.options} width="300" height="200" />

                this is test view
            </div>
        )
    }
}


export default TestView;


